/**
 * 图标处理工具类
 * 支持生成ICO、ICNS等格式的图标文件
 */
export class IconUtil {
  /**
   * 将Canvas转换为ICO格式的Blob
   * @param {HTMLCanvasElement} canvas - 画布元素
   * @param {Array} sizes - 尺寸数组，如[16, 32, 48]
   * @returns {Promise<Blob>} ICO格式的Blob
   */
  static async canvasToICO(canvas, sizes = [16, 32, 48]) {
    const images = []
    
    // 为每个尺寸生成图像数据
    for (const size of sizes) {
      const resizedCanvas = this.resizeCanvas(canvas, size, size)
      const imageData = resizedCanvas.getContext('2d').getImageData(0, 0, size, size)
      images.push({
        width: size,
        height: size,
        data: imageData.data
      })
    }
    
    return this.createICOBlob(images)
  }

  /**
   * 调整Canvas尺寸
   * @param {HTMLCanvasElement} sourceCanvas - 源画布
   * @param {number} width - 目标宽度
   * @param {number} height - 目标高度
   * @returns {HTMLCanvasElement} 调整后的画布
   */
  static resizeCanvas(sourceCanvas, width, height) {
    const canvas = document.createElement('canvas')
    const ctx = canvas.getContext('2d')
    
    canvas.width = width
    canvas.height = height
    
    // 使用高质量缩放
    ctx.imageSmoothingEnabled = true
    ctx.imageSmoothingQuality = 'high'
    
    ctx.drawImage(sourceCanvas, 0, 0, width, height)
    return canvas
  }

  /**
   * 创建ICO格式的Blob
   * @param {Array} images - 图像数据数组
   * @returns {Blob} ICO格式的Blob
   */
  static createICOBlob(images) {
    // ICO文件头结构
    const header = new ArrayBuffer(6)
    const headerView = new DataView(header)
    
    // ICO文件标识
    headerView.setUint16(0, 0, true) // Reserved
    headerView.setUint16(2, 1, true) // Type (1 = ICO)
    headerView.setUint16(4, images.length, true) // Number of images
    
    // 计算目录条目
    const directorySize = images.length * 16
    const directory = new ArrayBuffer(directorySize)
    const directoryView = new DataView(directory)
    
    let imageDataOffset = 6 + directorySize
    const imageDataBuffers = []
    
    images.forEach((image, index) => {
      // 转换RGBA到BMP格式
      const bmpData = this.createBMPData(image)
      imageDataBuffers.push(bmpData)
      
      const offset = index * 16
      
      // 目录条目
      directoryView.setUint8(offset + 0, image.width === 256 ? 0 : image.width) // Width
      directoryView.setUint8(offset + 1, image.height === 256 ? 0 : image.height) // Height
      directoryView.setUint8(offset + 2, 0) // Color palette
      directoryView.setUint8(offset + 3, 0) // Reserved
      directoryView.setUint16(offset + 4, 1, true) // Color planes
      directoryView.setUint16(offset + 6, 32, true) // Bits per pixel
      directoryView.setUint32(offset + 8, bmpData.byteLength, true) // Image size
      directoryView.setUint32(offset + 12, imageDataOffset, true) // Image offset
      
      imageDataOffset += bmpData.byteLength
    })
    
    // 合并所有数据
    const totalSize = 6 + directorySize + imageDataBuffers.reduce((sum, buf) => sum + buf.byteLength, 0)
    const result = new Uint8Array(totalSize)
    
    let offset = 0
    
    // 复制头部
    result.set(new Uint8Array(header), offset)
    offset += header.byteLength
    
    // 复制目录
    result.set(new Uint8Array(directory), offset)
    offset += directory.byteLength
    
    // 复制图像数据
    imageDataBuffers.forEach(buffer => {
      result.set(new Uint8Array(buffer), offset)
      offset += buffer.byteLength
    })
    
    return new Blob([result], { type: 'image/x-icon' })
  }

  /**
   * 创建BMP格式的图像数据
   * @param {Object} image - 图像对象 {width, height, data}
   * @returns {ArrayBuffer} BMP格式数据
   */
  static createBMPData(image) {
    const { width, height, data } = image
    const rowSize = Math.floor((width * 32 + 31) / 32) * 4
    const pixelDataSize = rowSize * height
    const maskDataSize = Math.floor((width + 31) / 32) * 4 * height
    const totalSize = 40 + pixelDataSize + maskDataSize
    
    const buffer = new ArrayBuffer(totalSize)
    const view = new DataView(buffer)
    
    // BMP信息头 (40 bytes)
    view.setUint32(0, 40, true) // Header size
    view.setInt32(4, width, true) // Width
    view.setInt32(8, height * 2, true) // Height (doubled for ICO)
    view.setUint16(12, 1, true) // Planes
    view.setUint16(14, 32, true) // Bits per pixel
    view.setUint32(16, 0, true) // Compression
    view.setUint32(20, pixelDataSize + maskDataSize, true) // Image size
    view.setUint32(24, 0, true) // X pixels per meter
    view.setUint32(28, 0, true) // Y pixels per meter
    view.setUint32(32, 0, true) // Colors used
    view.setUint32(36, 0, true) // Important colors
    
    // 像素数据 (从底部开始，BMP格式)
    let offset = 40
    for (let y = height - 1; y >= 0; y--) {
      for (let x = 0; x < width; x++) {
        const pixelIndex = (y * width + x) * 4
        
        // BGRA格式
        view.setUint8(offset++, data[pixelIndex + 2]) // B
        view.setUint8(offset++, data[pixelIndex + 1]) // G
        view.setUint8(offset++, data[pixelIndex + 0]) // R
        view.setUint8(offset++, data[pixelIndex + 3]) // A
      }
      
      // 行填充
      while (offset % 4 !== 0) {
        view.setUint8(offset++, 0)
      }
    }
    
    // AND掩码 (透明度掩码)
    const maskRowSize = Math.floor((width + 31) / 32) * 4
    for (let y = height - 1; y >= 0; y--) {
      for (let x = 0; x < maskRowSize; x++) {
        view.setUint8(offset++, 0) // 全部不透明
      }
    }
    
    return buffer
  }

  /**
   * 将图片转换为ICNS格式 (macOS图标)
   * @param {HTMLCanvasElement} canvas - 画布元素
   * @param {Array} sizes - 尺寸数组
   * @returns {Promise<Blob>} ICNS格式的Blob
   */
  static async canvasToICNS(canvas, sizes = [16, 32, 64, 128, 256, 512, 1024]) {
    // ICNS格式比较复杂，这里简化处理
    // 实际应用中可能需要更完整的ICNS编码器
    const images = []
    
    for (const size of sizes) {
      const resizedCanvas = this.resizeCanvas(canvas, size, size)
      const dataUrl = resizedCanvas.toDataURL('image/png')
      const blob = await this.dataURLToBlob(dataUrl)
      
      images.push({
        size,
        data: await blob.arrayBuffer()
      })
    }
    
    return this.createICNSBlob(images)
  }

  /**
   * 创建简化的ICNS格式Blob
   * @param {Array} images - 图像数据数组
   * @returns {Blob} ICNS格式的Blob
   */
  static createICNSBlob(images) {
    // 简化的ICNS实现，实际上应该使用正确的ICNS格式
    // 这里返回最大尺寸的PNG作为替代
    const largestImage = images.reduce((prev, current) => 
      current.size > prev.size ? current : prev
    )
    
    return new Blob([largestImage.data], { type: 'image/png' })
  }

  /**
   * 将DataURL转换为Blob
   * @param {string} dataURL - DataURL字符串
   * @returns {Promise<Blob>} Blob对象
   */
  static dataURLToBlob(dataURL) {
    return new Promise((resolve) => {
      const arr = dataURL.split(',')
      const mime = arr[0].match(/:(.*?);/)[1]
      const bstr = atob(arr[1])
      let n = bstr.length
      const u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      resolve(new Blob([u8arr], { type: mime }))
    })
  }

  /**
   * 验证图标尺寸是否合理
   * @param {number} size - 尺寸
   * @returns {boolean} 是否合理
   */
  static isValidIconSize(size) {
    const validSizes = [16, 20, 24, 32, 40, 48, 64, 72, 96, 128, 256, 512, 1024]
    return validSizes.includes(size)
  }

  /**
   * 获取推荐的图标尺寸
   * @param {string} format - 格式 ('ico' 或 'icns')
   * @returns {Array} 推荐尺寸数组
   */
  static getRecommendedSizes(format) {
    if (format === 'ico') {
      return [16, 32, 48, 64, 128, 256]
    } else if (format === 'icns') {
      return [16, 32, 64, 128, 256, 512, 1024]
    }
    return [16, 32, 48, 64, 128, 256]
  }
}

export default IconUtil
