<template>
  <div class="icon-generator-container">
    <div class="page-header">
      <h1>图片转软件图标</h1>
      <p class="page-description">
        将图片转换为软件图标格式（ICO、ICNS），支持多种尺寸，一键打包下载
      </p>
    </div>

    <div class="main-content">
      <!-- 上传区域 -->
      <el-card class="upload-card">
        <div slot="header">
          <span>上传图片</span>
        </div>
        
        <el-upload
          class="upload-dragger"
          drag
          :show-file-list="false"
          :before-upload="handleFileUpload"
          accept="image/*"
          style="height: auto !important;"
        >
          <div v-if="!originalImage" class="upload-content">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将图片拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip">支持 JPG、PNG、GIF、WebP 等格式</div>
          </div>
          <div v-else class="preview-content">
            <img :src="originalImage" alt="预览图" class="preview-image">
            <div class="upload-overlay">
              <i class="el-icon-upload2"></i>
              <p>点击重新上传</p>
            </div>
          </div>
        </el-upload>
      </el-card>

      <!-- 配置区域 -->
      <el-card class="config-card" v-if="originalImage">
        <div slot="header">
          <span>图标配置</span>
        </div>
        
        <el-form :model="config" label-width="120px">
          <el-form-item label="图标格式">
            <el-checkbox-group v-model="config.formats">
              <el-checkbox label="ico">ICO (Windows)</el-checkbox>
              <el-checkbox label="icns">ICNS (macOS)</el-checkbox>
            </el-checkbox-group>
          </el-form-item>

          <el-form-item label="图标尺寸">
            <div class="size-options">
              <el-checkbox-group v-model="config.sizes">
                <el-checkbox v-for="size in availableSizes" :key="size" :label="size">
                  {{ size }}×{{ size }}
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </el-form-item>

          <el-form-item label="背景处理">
            <el-radio-group v-model="config.background">
              <el-radio label="transparent">保持透明</el-radio>
              <el-radio label="white">白色背景</el-radio>
              <el-radio label="black">黑色背景</el-radio>
              <el-radio label="custom">自定义颜色</el-radio>
            </el-radio-group>
            <el-color-picker 
              v-if="config.background === 'custom'" 
              v-model="config.customColor"
              style="margin-left: 10px;"
            ></el-color-picker>
          </el-form-item>

          <el-form-item label="图片质量">
            <el-slider
              v-model="config.quality"
              :min="0.1"
              :max="1"
              :step="0.1"
              show-tooltip
              :format-tooltip="formatQuality"
            ></el-slider>
          </el-form-item>

          <el-form-item>
            <el-button 
              type="primary" 
              @click="generateIcons"
              :loading="generating"
              :disabled="config.formats.length === 0 || config.sizes.length === 0"
            >
              <i class="el-icon-magic-stick"></i>
              {{ generating ? '生成中...' : '生成图标' }}
            </el-button>
          </el-form-item>
        </el-form>
      </el-card>

      <!-- 预览区域 -->
      <el-card class="preview-card" v-if="generatedIcons.length > 0">
        <div slot="header">
          <span>生成预览</span>
          <el-button 
            style="float: right; padding: 3px 0" 
            type="text" 
            @click="downloadAll"
            :loading="downloading"
          >
            <i class="el-icon-download"></i>
            {{ downloading ? '打包中...' : '打包下载' }}
          </el-button>
        </div>

        <div class="icons-grid">
          <div 
            v-for="icon in generatedIcons" 
            :key="`${icon.format}-${icon.size}`"
            class="icon-item"
          >
            <div class="icon-preview">
              <img :src="icon.dataUrl" :alt="`${icon.format} ${icon.size}x${icon.size}`">
            </div>
            <div class="icon-info">
              <p class="icon-name">{{ icon.format.toUpperCase() }}</p>
              <p class="icon-size">{{ icon.size }}×{{ icon.size }}</p>
              <p class="icon-file-size">{{ formatFileSize(icon.size) }}</p>
            </div>
            <div class="icon-actions">
              <el-button size="mini" @click="downloadSingle(icon)">
                <i class="el-icon-download"></i>
              </el-button>
            </div>
          </div>
        </div>
      </el-card>

      <!-- 使用说明 -->
      <el-card class="help-card">
        <div slot="header">
          <span>使用说明</span>
        </div>
        
        <div class="help-content">
          <h4>支持的格式</h4>
          <ul>
            <li><strong>ICO</strong>: Windows 系统图标格式，支持多尺寸</li>
            <li><strong>ICNS</strong>: macOS 系统图标格式，支持 Retina 显示</li>
          </ul>

          <h4>推荐尺寸</h4>
          <ul>
            <li><strong>Windows ICO</strong>: 16×16, 32×32, 48×48, 64×64, 128×128, 256×256</li>
            <li><strong>macOS ICNS</strong>: 16×16, 32×32, 64×64, 128×128, 256×256, 512×512, 1024×1024</li>
          </ul>

          <h4>使用建议</h4>
          <ul>
            <li>建议使用正方形、高分辨率的原图</li>
            <li>图标设计应简洁明了，避免过多细节</li>
            <li>透明背景适合大多数应用场景</li>
            <li>生成的图标可直接用于软件开发</li>
          </ul>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import DownloadUtil from '@/util/DownloadUtil'
import FileUtil from '@/util/FileUtil'
import IconUtil from '@/util/IconUtil'
import JSZip from 'jszip'

export default {
  name: 'IconGeneratorView',
  data() {
    return {
      originalImage: null,
      originalFile: null,
      generating: false,
      downloading: false,
      generatedIcons: [],
      config: {
        formats: ['ico'],
        sizes: [16, 32, 48, 64, 128, 256],
        background: 'transparent',
        customColor: '#ffffff',
        quality: 0.9
      },
      availableSizes: [16, 20, 24, 32, 40, 48, 64, 72, 96, 128, 256, 512, 1024]
    }
  },
  mounted() {
    // 强制移除上传组件的固定高度
    this.$nextTick(() => {
      const uploadDraggers = document.querySelectorAll('.el-upload-dragger')
      uploadDraggers.forEach(dragger => {
        dragger.style.height = 'auto'
        dragger.style.minHeight = '200px'
      })
    })
  },
  methods: {
    handleFileUpload(file) {
      this.originalFile = file
      const reader = new FileReader()
      reader.onload = (e) => {
        this.originalImage = e.target.result
      }
      reader.readAsDataURL(file)
      return false // 阻止自动上传
    },

    formatQuality(value) {
      return `${Math.round(value * 100)}%`
    },

    formatFileSize(size) {
      // 估算文件大小
      const bytes = size * size * 4 // RGBA
      return FileUtil.formatBytes(bytes)
    },

    async generateIcons() {
      if (!this.originalImage) return
      
      this.generating = true
      this.generatedIcons = []

      try {
        const canvas = document.createElement('canvas')
        const ctx = canvas.getContext('2d')
        const img = new Image()
        
        await new Promise((resolve) => {
          img.onload = resolve
          img.src = this.originalImage
        })

        for (const format of this.config.formats) {
          for (const size of this.config.sizes) {
            const iconData = await this.generateSingleIcon(img, size, format, canvas, ctx)
            this.generatedIcons.push(iconData)
          }
        }

        this.$message.success(`成功生成 ${this.generatedIcons.length} 个图标`)
      } catch (error) {
        console.error('生成图标失败:', error)
        this.$message.error('生成图标失败: ' + error.message)
      } finally {
        this.generating = false
      }
    },

    async generateSingleIcon(img, size, format, canvas, ctx) {
      canvas.width = size
      canvas.height = size

      // 清除画布
      ctx.clearRect(0, 0, size, size)

      // 设置背景
      if (this.config.background !== 'transparent') {
        let bgColor = '#ffffff'
        switch (this.config.background) {
          case 'white':
            bgColor = '#ffffff'
            break
          case 'black':
            bgColor = '#000000'
            break
          case 'custom':
            bgColor = this.config.customColor
            break
        }
        ctx.fillStyle = bgColor
        ctx.fillRect(0, 0, size, size)
      }

      // 计算缩放比例，保持宽高比
      const scale = Math.min(size / img.width, size / img.height)
      const scaledWidth = img.width * scale
      const scaledHeight = img.height * scale
      const x = (size - scaledWidth) / 2
      const y = (size - scaledHeight) / 2

      // 绘制图片
      ctx.drawImage(img, x, y, scaledWidth, scaledHeight)

      let blob, dataUrl

      if (format === 'ico') {
        // 生成ICO格式
        blob = await IconUtil.canvasToICO(canvas, [size])
        dataUrl = canvas.toDataURL('image/png', this.config.quality)
      } else if (format === 'icns') {
        // 生成ICNS格式 (简化为PNG)
        dataUrl = canvas.toDataURL('image/png', this.config.quality)
        blob = await IconUtil.dataURLToBlob(dataUrl)
      } else {
        // 默认PNG格式
        dataUrl = canvas.toDataURL('image/png', this.config.quality)
        blob = await IconUtil.dataURLToBlob(dataUrl)
      }

      return {
        format,
        size,
        dataUrl,
        blob
      }
    },

    // 移除重复的方法，使用IconUtil中的方法

    downloadSingle(icon) {
      let extension = 'png'
      if (icon.format === 'ico') {
        extension = 'ico'
      } else if (icon.format === 'icns') {
        extension = 'icns'
      }
      const filename = `icon_${icon.size}x${icon.size}.${extension}`
      DownloadUtil.downloadBlob(icon.blob, filename)
    },

    async downloadAll() {
      if (this.generatedIcons.length === 0) return

      this.downloading = true
      try {
        const zip = new JSZip()
        
        // 按格式分组
        const formatGroups = {}
        this.generatedIcons.forEach(icon => {
          if (!formatGroups[icon.format]) {
            formatGroups[icon.format] = []
          }
          formatGroups[icon.format].push(icon)
        })

        // 添加文件到ZIP
        for (const [format, icons] of Object.entries(formatGroups)) {
          const folder = zip.folder(format.toUpperCase())
          for (const icon of icons) {
            let extension = 'png'
            if (format === 'ico') {
              extension = 'ico'
            } else if (format === 'icns') {
              extension = 'icns'
            }
            const filename = `icon_${icon.size}x${icon.size}.${extension}`
            folder.file(filename, icon.blob)
          }
        }

        // 生成ZIP文件
        const zipBlob = await zip.generateAsync({ type: 'blob' })
        const filename = DownloadUtil.generateTimestampFilename('app_icons', 'zip')
        DownloadUtil.downloadBlob(zipBlob, filename)
        
        this.$message.success('图标包下载成功')
      } catch (error) {
        console.error('打包下载失败:', error)
        this.$message.error('打包下载失败: ' + error.message)
      } finally {
        this.downloading = false
      }
    }
  }
}
</script>

